* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  background-color: #E5E5E5;
}

.container {
  max-width: 1200px;
  margin: 0px auto;
}

.information {
  width: 375px;
  background-color: #F1F1F1;
  position: relative;
}
.information__logo {
  position: absolute;
  top: 0;
  right: 0;
}
.information__img {
  margin-bottom: 12px;
}
.information__title {
  padding: 0 15px 10px;
}
.information__title > p {
  color: #454545;
  font-weight: bold;
  font-size: 18px;
  line-height: 129%;
}
.information__link {
  font-size: 15px;
  font-style: italic;
  color: #A5A5A5;
  padding: 0 0 18px 15px;
}
.information__link a {
  color: #1C8DDC;
  font-style: normal;
  font-weight: 700;
  text-decoration: none;
}